import "./App.css";
import { Layout, Form, Button, Input, DatePicker, Result } from "antd";
import { useState } from "react";
import moment from "moment";
import axios from "axios";
import FormItem from "antd/lib/form/FormItem";

const { Content } = Layout;

function App() {
  const [form] = Form.useForm();

  const [validationStatus, setValidationStatus] = useState({
    cardNumber: false,
    expDate: false,
    cvv: false,
    amount: false,
  });
  const [isTransferred, setIsTransferred] = useState(false);
  const [transactionId, setTransationId] = useState("");
  const [amount, setAmount] = useState(0);

  const monthFormat = "MM/YYYY";

  const [disableStatus, setDisableStatus] = useState(false);

  const buttonItemLayout = {
    wrapperCol: { span: 9, offset: 10 },
  };
  const errorMessage = "An error has occurred. The server is not responding.";
  const [showError, setShowError] = useState(false);

  function showform() {
    return (
      <div className="content_form">
        <div className="text">
          <p>Please enter your card details</p>
        </div>
        <Form
          form={form}
          labelCol={{ span: 9 }}
          wrapperCol={{ span: 10 }}
          labelWrap
          name="basic"
          onFinish={(values) => {
            async function transferFunds(values: any) {
              const transaction = {
                cardNumber: +values.cardNumber.replaceAll(/\s+/g, ""),
                expDate: values.expDate.format(monthFormat),
                cvv: +values.CVV,
                amount: +values.amount,
              };
              await axios
                .post("/transfer", transaction)
                .then((response: any) => {
                  const responseInfo = {
                    requestId: response.data.requestId,
                    amount: response.data.amount,
                  };
                  setTransationId(responseInfo.requestId);
                  setAmount(responseInfo.amount);
                  setIsTransferred(true);
                  return responseInfo;
                })
                .catch((error) => {
                  setShowError(true);
                  setDisableStatus(false);
                });
            }
            transferFunds(values);
          }}
          onFinishFailed={() => {
            setDisableStatus(false);
          }}
          onValuesChange={(changed, values) => {
            if (changed.cardNumber) {
              let cardNumVal = changed.cardNumber.replaceAll(/\s+/g, "");
              cardNumVal = cardNumVal.replaceAll(/\D+/g, "");
              const res = [
                cardNumVal.slice(0, 4),
                cardNumVal.slice(4, 8),
                cardNumVal.slice(8, 12),
                cardNumVal.slice(12, 16),
              ]
                .join(" ")
                .replaceAll(/\s+/g, " ")
                .trim();
              form.setFieldsValue({
                ["cardNumber"]: res,
              });
            }

            if (changed.CVV) {
              let cvvVal = changed.CVV.replaceAll(/\s+/g, "");
              cvvVal = cvvVal.replaceAll(/\D+/g, "");
              if (cvvVal.length > 3) {
                cvvVal = cvvVal.slice(0, 3);
              }
              form.setFieldsValue({
                ["CVV"]: cvvVal,
              });
            }

            if (changed.amount) {
              let amountVal = changed.amount.replaceAll(/\s+/g, "");
              amountVal = amountVal.replaceAll(/[a-zA-Zа-яА-Я]/g, "");
              form.setFieldsValue({
                ["amount"]: amountVal,
              });
            }
          }}
        >
          <Form.Item
            className="form_color"
            name="cardNumber"
            label={
              <label style={{ color: "white", fontWeight: "bold" }}>
                Card Number
              </label>
            }
            rules={[
              { required: true, message: "Please enter your card number" },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  let parsedVal = value.replace(/\s+/g, "");
                  if (/^\d{16}$/.test(parsedVal)) {
                    setValidationStatus({
                      ...validationStatus,
                      cardNumber: true,
                    });
                    return Promise.resolve();
                  } else {
                    return Promise.reject(
                      "Your card number should consist of 16 digits"
                    );
                  }
                },
              }),
              { whitespace: true },
            ]}
            hasFeedback
          >
            <Input placeholder="0000 0000 0000 0000" disabled={disableStatus} />
          </Form.Item>
          <Form.Item
            name="expDate"
            label={
              <label style={{ color: "white", fontWeight: "bold" }}>
                Expiration Date
              </label>
            }
            rules={[
              {
                required: true,
                message: "Please insert expiration date of your card",
              },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  setValidationStatus({ ...validationStatus, expDate: true });
                  return Promise.resolve();
                },
              }),
            ]}
            hasFeedback
          >
            <DatePicker
              format={monthFormat}
              picker="month"
              placeholder={monthFormat}
              disabled={disableStatus}
              disabledDate={(current) =>
                current && current < moment().endOf("month")
              }
            />
          </Form.Item>
          <Form.Item
            name="CVV"
            label={
              <label style={{ color: "white", fontWeight: "bold" }}>CVV</label>
            }
            rules={[
              { required: true, message: "Please insert CVV of your card." },
              { min: 3, max: 3 },

              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (/^\d{3}$/.test(value)) {
                    setValidationStatus({ ...validationStatus, cvv: true });
                    return Promise.resolve();
                  } else {
                    return Promise.reject("CVV should consist of 3 digits.");
                  }
                },
              }),
            ]}
            hasFeedback
          >
            <Input placeholder="---" disabled={disableStatus} />
          </Form.Item>
          <Form.Item
            name="amount"
            label={
              <label style={{ color: "white", fontWeight: "bold" }}>
                Amount (USD)
              </label>
            }
            rules={[
              {
                required: true,
                message: "Please specify the amount to be transferred.",
              },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (+value) {
                    setValidationStatus({
                      ...validationStatus,
                      amount: true,
                    });
                    return Promise.resolve();
                  } else {
                    return Promise.reject(
                      "Transferable amount should consist of digits."
                    );
                  }
                },
              }),
            ]}
            normalize={(e) => {
              if (e.indexOf(".") > -1) {
                let index = e.indexOf(".");
                let firstPart = e.slice(0, index);
                let secPart = e.slice(index + 1);
                if (secPart.length > 2) {
                  secPart = secPart[0] + secPart[1];
                }
                return firstPart + "." + secPart;
              } else {
                return e;
              }
            }}
            hasFeedback
          >
            <Input placeholder="1000.00" disabled={disableStatus} />
          </Form.Item>
          <FormItem {...buttonItemLayout}>
            <Button
              type="primary"
              style={{
                background: "#857b83",
                borderColor: "#3f4855",
                margin: "0px, center",
              }}
              htmlType="submit"
              onClick={() => setDisableStatus(true)}
              disabled={
                !(
                  validationStatus.cardNumber &&
                  validationStatus.expDate &&
                  validationStatus.cvv &&
                  validationStatus.amount
                )
              }
            >
              Pay
            </Button>
          </FormItem>
        </Form>
        <div className="text error">{showError ? errorMessage : ""}</div>
      </div>
    );
  }

  function showResult() {
    return (
      <div className="content_result">
        <Result
          style={{ color: "white" }}
          status="success"
          title="Your transaction was successfully completed!"
          subTitle={`Transaction ID: ${transactionId} Transferred Amount ${amount}`}
        />
        <div className="content_result_goBack">
          <p
            onClick={() => {
              setIsTransferred(!isTransferred);
              form.resetFields();
              setDisableStatus(!disableStatus);
              setValidationStatus({
                cardNumber: false,
                expDate: false,
                cvv: false,
                amount: false,
              });
            }}
          >
            Go to payment form
          </p>
        </div>
      </div>
    );
  }

  return (
    <Content className="content">
      <div className="content_image">
        {isTransferred ? showResult() : showform()}
      </div>
    </Content>
  );
}

export default App;
